@import "../../../colors.scss";

.title {
  font-size: 1.52em;
  font-weight: 400;
  margin-bottom: 14px;
}

.subtitle {
  font-size: 1em;
  margin-bottom: 14px;
}

.blue {
  display: block;
  color: $light-blue;
  font-weight: 500;
  margin-bottom: 18px;
  a {
    color: $light-blue;
  }
}

.link {
  color: $light-blue;
  display: block;
  text-decoration: none;
  margin-bottom: 10px;
  &:hover {
    text-decoration: underline;
  }
}
